                {/* <p className="output-container" style={{width: '35vw', marginTop: '80px'}} >Du kannst in der Eingabemaske deine Inputvariablen anpassen und die Berechnung erneut starten, um zu sehen wie sich der Output ver√§ndert.</p> */}
